.viewer {
    width: 100%;
    height: 100vh;
    position: relative;

    &__navBar {
        position: fixed;
        z-index: 100;
        height: 90%;
        width: 20%;
        left: 2%;
        top: 5%;
        background-color: rgba(17, 34, 51, 0.356);
        color: white;

        &__item {
            display: flex;
            justify-content: center;
            align-items: center;

            padding: 10px;
            position: relative;

            .desc {
                padding: 0 10px;
                width: 40%;
                text-align: right;
            }

            .form-control {
                width: 60%;
            }
        }
    }
}

model-viewer {
    width: 100%;
    height: 100vh;
    background-color: #70bcd1;
    --poster-color: #ffffff00;
}

body {
    margin: 0;
    overflow: hidden;
}